import "./App.css";
import React, { Component, useState } from "react";
import FileViewer from "./FileViewer";
function App() {
  const [num1, setNum1] = useState("");
  const [num2, setNum2] = useState("");
  const [num3, setNum] = useState(0);

  return (
    <div>
      <div className="container-1">
        <input type="text"  className="type-1" value={num1} onChange={(num1) => setNum1(num1.target.value)}></input>

        <div className="type-1">+</div>

        <input type="text" className="type-1" value={num2} onChange={(num2) => { setNum2(num2.target.value);}}></input>

        <div className="type-1">=</div>

       
        <h1 className="type-2">
          {num2 ? parseInt(num1) + parseInt(num2) : ""}
        </h1>
      </div>
      <div className="container">
        <button className="btn">
          Upload File <FileViewer></FileViewer>
        </button>
      </div>
    </div>
  );
}
export default App;
